<template>
  <transition name="drop">
    <div class="result-wrapper">
      <spots/>
      <div class="result">
        <desc-item text="姓名" :value="result.name"/>
        <desc-item text="个人技能" :value="result.skill"/>
        <desc-item text="手机号码" :value="result.phTelphone"/>
        <desc-item text="联系方式" :value="result.call"/>
        <desc-item text="匹配度" :value="result.value"/>
        <protectBtn @tap="tapHandler" class="btn"/>
      </div>
    </div>
  </transition>
</template>

<script>
import Desc from 'base/desc/desc'
import Spots from 'base/spots/spots'
import ProtectBtn from 'base/protectBtn/protectBtn'

export default {
  data () {
    return {
      result: this.$eventBus.result
    }
  },
  created () {
    this.back()
  },
  methods: {
    back () {
      if (this.result === null) {
        this.$router.back()
      }
    },
    tapHandler () {
      this.$router.push({
        path: '/tips'
      })
    }
  },
  components: {
    DescItem: Desc,
    Spots,
    ProtectBtn
  }
}
</script>

<style lang="stylus">
@import '~common/stylus/theme'

  .drop-enter-active
    transition: transform .4s cubic-bezier(0.86, 0.18, 0.82, 2.1)
  .drop-enter
    transform: translate3d(0, -300px, 0)
  .result-wrapper
    width: 100%
    height: 100%
    display: flex
    align-items: center
    justify-content: center
    flex-direction: column
    .result
      position: relative
      .btn
        position: absolute
        bottom: -50px
        right: 0
</style>




