<template>
  <div class="protect" @click="clickHandler">
    <i class="iconfont icon-archer"></i>
    <span class="text">
      防骗小贴士
    </span>
  </div>
</template>

<script>
export default {
  methods: {
    clickHandler () {
      this.$emit('tap')
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~common/stylus/theme'

  .protect
    cursor: pointer
    .iconfont
      display: inline-block
      transform: rotate(10deg)
      color: $text-color
      font-size: $font-size-large-xxl
    .text
      font-size: $font-size-medium
      padding-bottom: 1px
      border-bottom: 1px solid $text-color
</style>




